.nav {
  visibility: hidden;
  opacity: 0;
  z-index: 50;
}

@media screen and (max-width: 479px) {
  .nav {
    width: 250px;
  }
}

@media screen and (min-width: 480px) and (max-width: 1279px) {
  .nav {
    width: 40%;
  }
}

@media screen and (max-width: 1279px) {
  .nav {
    height: 100vh;
    background-color: #44c697;
    padding: 10px 0;
    transition-property: transform, visibility, opacity;
    transition-duration: .5s;
    transition-timing-function: cubic-bezier(.445, .05, .55, .95);
    position: fixed;
    top: 0;
    left: 100%;
    box-shadow: -8px 0 20px rgba(0, 0, 0, .25);
  }
}

.nav.is-open {
  visibility: visible;
  opacity: 1;
  transform: translateX(-100%);
}

@media screen and (min-width: 1280px) {
  .nav {
    visibility: visible;
    opacity: 1;
    display: inline-block;
  }
}

@media screen and (min-width: 1280px) {
  .nav-list {
    justify-content: space-between;
    display: flex;
  }
}

@media screen and (max-width: 1279px) {
  .nav-list__item {
    height: 42px;
    align-items: center;
    padding-left: 24px;
    display: flex;
  }

  .nav-list__item:nth-child(2n+1) {
    border-top: 1px solid #1e2e36;
    border-bottom: 1px solid #1e2e36;
  }
}

@media screen and (min-width: 1280px) {
  .nav-list__item:not(:last-child) {
    margin-right: 30px;
  }
}

.nav-list__link {
  color: #fff;
  text-transform: uppercase;
  font-family: Proxima Nova, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: bold;
  line-height: 1.29;
  text-decoration: none;
  transition-property: color;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.nav-list__link:hover, .nav-list__link:focus, .nav-list__link--current {
  color: #1e2e36;
}

@media screen and (min-width: 1280px) {
  .nav-list__link {
    color: #1e2e36;
    font-size: 18px;
    line-height: 1.31;
  }

  .nav-list__link:hover, .nav-list__link :focus {
    border-bottom: 3px solid #44c697;
  }
}

